@import "../../common/styles/variables";

.skillsBlock {
  background: var(--theme--bgColorSkills);
}

.skills {
  border: 1px solid;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px 0;

  @media (max-width: 750px) {
    justify-content: center;
  }
}

